@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif;
  background-color: rgb(241, 239, 239);
  color: rgb(19, 18, 18);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.navbar{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    background-color: white;
    align-items: center;
}

h2 {
    text-align: center;
    font-weight: 900;
}

.navbar-logo{
    width: 10%;    
    margin-right: 2%;
}

.navbar-links{
    list-style: none;
    display: flex;
    flex-wrap: nowrap;
    flex:1 1;
    margin: 0px;
    justify-content: center ;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

.navbar-links-item {
    padding: 0% 4%;
    font-weight: 800;
    text-align: center;
}
.navbar-links-item :hover {
    color: rgb(16, 58, 16)
}

.navbar-links-item > * {
    text-decoration: none;
    color: black;
}

@media only screen and (max-width: 600px) {
    [class*="navbar-logo"]{
        width: 20%
    }
}

.property-cards{
    max-width: 30%;
    display: flex;
    flex-direction: column;
    padding: 1%;
    margin: 0.5%;
    border: solid 1px rgb(5, 107, 5);
    flex:1 1 30%;
    justify-content: space-around;

    
}
.logo-container{
   background-color: ivory;
   padding: 0%;
   display: flex;
   justify-content: center;
}

.logo{
    width:15%;
}

.title{
    padding: 2% 2% 0% 2%;
    font-weight: 800;
}

.city-type {
    padding: 2% 2% 0% 2%;
    font-style: italic;
}

.bedrooms,.bathrooms{
    padding: 2% 2% 0% 2%;
}

.price{
    padding: 2%;
    font-style: bold;
    font-weight: 800;
}

.email{ 
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: rgb(5, 107, 5);
    flex-wrap: nowrap;
}

#iconId{
    color: chartreuse;  
}

a {
     margin-right: 1%;
}

@media only screen and (max-width: 768px) {
    [class*="property-cards"]{
        flex:1 1 50%; 
        max-width: 45%;
    }
}

@media only screen and (max-width: 600px) {
    [class*="property-cards"]{
        flex:1 1 100%; 
        max-width: 95%;
    }
}
.sidebar-links {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.sidebar-links-items {
    margin-left: 5%;
    padding: 5px 0%;
    text-decoration: none;
    color: ivory;
}

h4 {
    margin-left: 5%;
}

.search {
    display: flex;
    margin: 5%;
    justify-content: center;
}

.search-input {
   width: 65%;
   padding: 6%;
   border-radius: 8px 0px 0px 8px;
   background-color:ivory;
   border: 1px solid rgb(9, 32, 2);
   font-weight: 600;

}

.search-button {
    background-color:ivory;
    border-radius: 0px 8px 8px 0px;
    border: 1px solid rgb(9, 32, 2);
    cursor: pointer;
    padding: 0% 5%;
    font-weight: 400;
    justify-content: center;
}

.reset-button {
    text-align: center;
    margin-left: 5%;
    margin-top: 10%;
}






.alert{
    font-weight: 600;
    text-align: center;
}

.error{
    background-color: rgb(187, 83, 83);
    border: 1px solid rgb(1, 44, 1)
}

.success{
    background-color: green;
    border: 1px solid rgb(1, 44, 1);
}
.properties {
    padding: 2%;
    display: flex;
    flex-wrap: wrap;
    width: 80%;
}

.full-page {
   display: flex;

}

.sidebar {
    background-color: rgb(59, 105, 12);
    width: 20%;
}

@media only screen and (max-width: 600px) {
    [class*="full-page"]{
        flex-wrap: wrap; 
    }
    [class*='sidebar']{
        width: 100%;
        margin: 5%
    }
    [class*='properties']{
        width: 100%;
    }
}
.AddProperty {
    padding:2% 25%;

}

.input,.select {
    color: rgb(10, 31, 10) ;
    width: 100%;
    border: 1px solid  rgb(5, 107, 5);
    margin: 0.5% 1% 2% 1%;
    padding: 2%;
    box-sizing: border-box;
    font-weight: 600;
    background-color:ivory;
}

.add-button {
    background-color:ivory;
    border-radius: 8px;
    border: 1px solid rgb(9, 32, 2);
    cursor: pointer;
    padding: 2% 10%;
    font-weight: 600;
    margin-left: 1%;
  }

.label{
    padding: 1%;
}

.add-button:hover {
    background-color: rgb(19, 246, 27);
  }

::-webkit-input-placeholder {
    color: rgb(10, 31, 10);
}

:-ms-input-placeholder {
    color: rgb(10, 31, 10);
}

::-ms-input-placeholder {
    color: rgb(10, 31, 10);
}

::placeholder {
    color: rgb(10, 31, 10);
}


@media only screen and (max-width: 768px) {
    [class*="input"]{
        width: 100%;    
    }
    [class*="select"]{
        width: 100%;    
    }
    [class*="AddProperty"]{
        padding:2% 10%;
    }
}

